export var isAwsAccountIdValid = function (awsAccountId) {
    return (awsAccountId !== undefined &&
        /^\d+$/.test(awsAccountId) &&
        awsAccountId.length === 12);
};
export var isGcpEmailValid = function (gcpShareWithAccount) {
    return (gcpShareWithAccount !== undefined &&
        /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,12}$/.test(gcpShareWithAccount) &&
        gcpShareWithAccount.length <= 253);
};
